function FaqData({ list, index }) {
  const [answerShow, setAnswerShow] = useState(false)
  return (
    <div className="card" key={index}>
      <div className="card-header" onClick={() => setAnswerShow(!answerShow)}>
        <h5 className="mb-0">
          {list.question}
        </h5>
        {
          answerShow === false ?
            <div className="plus-icon"> <BiPlusMedical /></div>
            :
            <div className="minus-icon">  <FaMinus /></div>
        }
      </div>
      {
        answerShow === true &&
        <div className="card-body">
          <p>{list.answer}</p>
        </div>
      }
    </div>
  )
}
